.filters,
.sorting {
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.sorting {
  max-width: 600px;
  margin: auto;
}

.filters .filter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 15px;
}

.FiltersBox {
  padding-top: 15px;
}

.filter label {
  margin: 0 10px 0 0;
  font-weight: 900;
}

.filter select,
.filter input[type='number'],
.filter input[type='date'] {
  background-color: #FFFFFF;
  display: block;
  height: 30px;
  padding: 6px 12px;
  font-size: 16px;
  line-height: 1.42;
  color: #555;
  width: 100%;
  background-repeat: no-repeat;
  background-image: none;
  border: 1px solid #e4e4e4;
  font-weight: 600;
  border-radius: 4px;
}



